.side-div {
    /* text-align: center; */
    /* height: 100%; */
    width: 10%;
    min-height: 100vh;;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #333333;
    outline: thick red;
}

.side-nav {
    display: flex;
    align-items: space-evenly;
    justify-content: space-evenly;
    flex-direction: column;
    height: 100%;
    outline: thick red;
    /* background-color: aqua; */
}

ul.no-bullets {
    display:flex;
    flex-direction: column;
    align-items: space-evenly;
    justify-content: space-evenly;
    height: 100%;
    list-style-type: none; /* Remove bullets */
    margin: 0;
    padding: 0;
    overflow: hidden;
    /* background-color: #333333; */
    /* background-color: aqua; */
    outline: thick red;
  }
  
  li {
    float: left;
    outline: thick red;
  }
  
  li a {
    display: block;
    color: white;
    text-align: center;
    padding: 16px;
    text-decoration: none;
    outline: thick red;
  }
  
  li a:hover {
    background-color: #111111;
  }